<template>
  <div class="add-new-user-page container">
    <!-- +   -->
    <div class="page-header row">
      <div class="header-left col-md-4">
        <router-link to="/Users_1">
            <img src="./left.png" alt="">
        </router-link>
      </div>
      <h1 class="page-title col-md-4 text-center">{{ pageTitle }}</h1>
      <div class="header-right col-md-4"></div>
    </div>
    
    <!--  -->
    <form class="user-form">
      <!-- 3 -->
      <div class="form-row row">
        <div class="col-md-4">
          <div class="form-group">
            <label for="firstName">First Name</label>
            <input type="text" class="form-control" id="firstName" placeholder="Enter First Name" v-model="form.firstName">
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="lastName">Last Name</label>
            <input type="text" class="form-control" id="lastName" placeholder="Enter Last Name" v-model="form.lastName">
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="userId">User ID *</label>
            <input type="text" class="form-control" id="userId" placeholder="Enter User ID" v-model="form.userId">
          </div>
        </div>
      </div>
      
      <!-- 3 -->
      <div class="form-row row">
        <div class="col-md-4">
          <div class="form-group">
            <label for="badgeId">Badge ID</label>
            <input type="text" class="form-control" id="badgeId" placeholder="Enter Badge ID" v-model="form.badgeId">
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="pin">Pin # *4 digits minimum</label>
            <input type="text" class="form-control" id="pin" placeholder="Enter your pin" v-model="form.pin">
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="role">Role *</label>
            <select class="form-control" id="role" v-model="form.role">
              <option value="">Select Role</option>
              <option value="temporary">Temporary</option>
              <option value="fullTime">Full Time</option>
            </select>
          </div>
        </div>
      </div>
      
      <!--  -->
      <div class="form-group device-management-group">
        <label>Note: Will Show list of all selected device types for the user.</label>
        <div class="device-management-label">Device Management</div>
        <div class="radio" v-for="option in deviceManagementOptions" :key="option.value">
          <label style="display: flex; align-items: center; gap: 10px; flex-wrap: wrap;">
            <input type="radio" name="deviceManagement" :value="option.value" v-model="form.deviceManagement">
            {{ option.label }}
            <!-- Assigned LockerSelect Locker -->
            <div class="locker-button-container" v-if="option.value === 'assignedLocker' && form.deviceManagement === 'assignedLocker'">
              <button 
                type="button" 
                class="btn btn-secondary" 
                @click="showLockerDialog = true"
              >
                {{ form.assignedLocker ? `Selected: ${form.assignedLocker.name}` : 'Select Locker' }}
              </button>
            </div>
            
            <!-- GroupsSelect Group -->
            <div class="group-button-container" v-if="option.value === 'groups' && form.deviceManagement === 'groups'">
              <button 
                type="button" 
                class="btn btn-secondary" 
                @click="showGroupDialog = true"
              >
                Select Group
              </button>
            </div>
          </label>
        </div>
        
        <!-- Groups -->
        <div class="groups-display-area" v-if="form.deviceManagement === 'groups'">
          <div v-if="selectedGroups.length > 0" class="selected-groups">
            <div class="group-tag" v-for="group in selectedGroups" :key="group.id">
              {{ group.name }}
              <span class="remove-group" @click="removeGroup(group.id)"></span>
            </div>
          </div>
          <div v-else class="empty-state">No groups selected</div>
        </div>
      </div>
      
      <!--  -->
      <div class="form-group last-group">
        <label>Multiple Device Check-Out *</label>
        <div class="row inline-group">
          <!-- col-md-4col-md-8flex -->
          <div class="radio-group-container">
            <div class="radio radio-inline">
              <label>
                <input type="radio" name="multipleDeviceCheckout" value="no" v-model="form.multipleDeviceCheckout">
                No
              </label>
            </div>
            <div class="radio radio-inline">
              <label style="margin-top: 8px;">
                <input type="radio" name="multipleDeviceCheckout" value="yes" v-model="form.multipleDeviceCheckout">
                Yes
              </label>
            </div>
            
            <!-- yes -->
            <div class="device-count-selector" v-if="form.multipleDeviceCheckout === 'yes'">
              <select class="form-control" v-model="form.deviceCount">
                <option value="">Select Count</option>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
              </select>
            </div>
          </div>
        </div>
      </div>
      
      <!--  -->
      <div class="save-btn-container text-right">
        <button type="button" class="btn btn-primary" @click="showConfirmDialog">Save</button>
      </div>
    </form>
    
    <!--  -->
    <div class="modal fade" :class="{ 'in': dialogVisible }" :style="{ display: dialogVisible ? 'block' : 'none' }">
      <div class="modal-dialog" style="width: 450px;margin: 10% auto;">
        <div class="modal-content" style="border-radius: 12px; box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);">
          <div class="modal-body" style="text-align: center; padding: 30px 24px;">
            <div class="dialog-icon" style="margin-bottom: 18px;">
              <span class=" " style="font-size: 45px; color: #ff4d4f;">
                <img src="./1-1.png" alt="">
              </span>
            </div>
            <p class="dialog-text">
              <strong style="display: block; font-size: 1.8rem; margin-bottom: 18px;">Are you Sure?</strong>
              <strong style="font-weight: 700; color: #000 !important;">Do you really want to delete this User "{{ form.firstName }} {{ form.lastName }}"?</strong>
            </p>
            <p class="dialog-text" style="font-weight: 700; color: #000 !important;">This process cannot be Undone.</p>
          </div>
          <div class="modal-footer" style="justify-content: center; border-top: none; padding: 12px 15px 30px;">
            <button type="button" class="btn btn-primary" @click="dialogVisible = false">Cancel</button>
            <button type="button" class="btn btn-danger" @click="handleConfirmDelete">Delete</button>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Locker -->
    <div class="modal fade" :class="{ 'in': showLockerDialog }" :style="{ display: showLockerDialog ? 'block' : 'none' }">
      <div class="modal-dialog locker-modal">
        <div class="modal-content">
          <div class="modal-header locker-header">
            <h4 class="modal-title">Select Locker</h4>
            <div class="search-bar">
              <input 
                type="text" 
                v-model="searchQuery" 
                placeholder="Search lockers..." 
                class="locker-search"
                @input="filterLockers"
              >
            </div>
          </div>
          <div class="modal-body locker-body">
            <p class="selection-prompt">Please select the locker</p>
            
            <div class="lockers-grid">
              <button 
                type="button" 
                class="locker-item" 
                v-for="locker in filteredLockers" 
                :key="locker.id"
                :class="{ 'selected': selectedLockerId === locker.id }"
                @click="selectLocker(locker.id)"
              >
                {{ locker.name }}
              </button>
              
              <!-- Unlimited -->
              <button 
                type="button" 
                class="locker-item unlimited"
                :class="{ 'selected': selectedLockerId === 'unlimited' }"
                @click="selectLocker('unlimited')"
              >
                Unlimited
              </button>
            </div>
          </div>
          <div class="modal-footer locker-footer">
            <button type="button" class="btn cancel-btn" @click="showLockerDialog = false">Cancel</button>
            <button type="button" class="btn confirm-btn" @click="confirmLockerSelection">Confirm</button>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Group -->
    <div class="modal fade" :class="{ 'in': showGroupDialog }" :style="{ display: showGroupDialog ? 'block' : 'none' }">
      <div class="modal-dialog group-modal">
        <div class="modal-content">
          <div class="modal-header group-header">
            <h4 class="modal-title">Select Groups</h4>
            <div class="search-bar">
              <input 
                type="text" 
                v-model="groupSearchQuery" 
                placeholder="Search groups..." 
                class="group-search"
                @input="filterGroups"
              >
            </div>
          </div>
          <div class="modal-body group-body">
            <p class="selection-prompt">Please select one or more groups</p>
            
            <div class="groups-grid">
              <button 
                type="button" 
                class="group-item" 
                v-for="group in filteredGroups" 
                :key="group.id"
                :class="{ 'selected': isGroupSelected(group.id) }"
                @click="toggleGroupSelection(group.id)"
              >
                {{ group.name }}
              </button>
            </div>
          </div>
          <div class="modal-footer group-footer">
            <button type="button" class="btn cancel-btn" @click="showGroupDialog = false">Cancel</button>
            <button type="button" class="btn confirm-btn" @click="confirmGroupSelection">Confirm</button>
          </div>
        </div>
      </div>
    </div>
    
    <!--  -->
    <div class="modal-backdrop fade" :class="{ 'in': dialogVisible || showLockerDialog || showGroupDialog }" :style="{ display: (dialogVisible || showLockerDialog || showGroupDialog) ? 'block' : 'none' }" v-if="dialogVisible || showLockerDialog || showGroupDialog"></div>
  </div>
</template>

<script>
import Vue from 'vue';

export default {
  data() {
    return {
      pageTitle: 'Add New User',
      dialogVisible: false,
      showLockerDialog: false,
      showGroupDialog: false, // 
      // 
      searchQuery: '',
      groupSearchQuery: '', // 
      filteredLockers: [],
      filteredGroups: [], // 
      // locker
      lockers: [
        { id: 1, name: 'Locker 01' },
        { id: 2, name: 'Locker 02' },
        { id: 3, name: 'Locker 03' },
        { id: 4, name: 'Locker 04' },
        { id: 5, name: 'Locker 05' },
        { id: 6, name: 'Locker 06' },
        { id: 7, name: 'Locker 07' },
        { id: 8, name: 'Locker 08' },
        { id: 9, name: 'Locker 09' },
        { id: 10, name: 'Locker 10' }
      ],
      // 
      groups: [
        { id: 1, name: 'Group 01' },
        { id: 2, name: 'Group 02' },
        { id: 3, name: 'Group 03' },
        { id: 4, name: 'Group 04' },
        { id: 5, name: 'Group 05' },
        { id: 6, name: 'Group 06' },
        { id: 7, name: 'Group 07' },
        { id: 8, name: 'Group 08' },
        { id: 9, name: 'Group 09' },
        { id: 10, name: 'Group 10' }
      ],
      selectedLockerId: '',
      selectedGroupIds: [], // ID
      selectedGroups: [], // 
      form: {
        firstName: '',
        lastName: '',
        userId: '',
        badgeId: '',
        pin: '',
        role: '',
        deviceManagement: 'anyLocker',
        multipleDeviceCheckout: 'yes',
        deviceCount: '',
        assignedLocker: null
      },
      deviceManagementOptions: [
        { label: 'Any Locker', value: 'anyLocker' },
        { label: 'Assigned Locker', value: 'assignedLocker' },
        { label: 'Groups', value: 'groups' }
      ]
    };
    
  },
  created() {
    this.updatePageTitle();
    // lockergroup
    this.filteredLockers = [...this.lockers];
    this.filteredGroups = [...this.groups]; // 
    
    const mode = this.$route.query.mode;
    if (mode === 'edit') {
      this.pageTitle = 'Edit User';
    } else {
      this.pageTitle = 'Add New User';
    }
    
    const editUser = this.$route.params.user;
    if (editUser) {
      this.pageTitle = 'Edit User';
      const [firstName, ...lastNameParts] = editUser.name.split(' ');
      this.form = {
        ...this.form,
        firstName: firstName || '',
        lastName: lastNameParts.join(' ') || '',
        userId: editUser.userId,
        role: editUser.role,
      };
    }
  
  },
  watch: {
    $route: {
      handler() {
        this.updatePageTitle();
      },
      immediate: true
    }
  },
  methods: {
    updatePageTitle() {
      const mode = this.$route.query.mode;
      if (mode === 'edit') {
        this.pageTitle = 'Edit User';
      } else {
        this.pageTitle = 'Add New User';
      }
    },
    showConfirmDialog() {
      this.dialogVisible = true;
    },
    handleConfirmDelete() {
      console.log('Saving user data:', this.form);
      this.dialogVisible = false;
    },
    handleBack() {
      console.log('');
    },
    // locker
    filterLockers() {
      if (!this.searchQuery) {
        this.filteredLockers = [...this.lockers];
      } else {
        const query = this.searchQuery.toLowerCase();
        this.filteredLockers = this.lockers.filter(locker => 
          locker.name.toLowerCase().includes(query)
        );
      }
    },
    // locker
    selectLocker(lockerId) {
      this.selectedLockerId = lockerId;
    },
    // locker
    confirmLockerSelection() {
      if (this.selectedLockerId) {
        if (this.selectedLockerId === 'unlimited') {
          this.form.assignedLocker = { id: 'unlimited', name: 'Unlimited' };
        } else {
          const selectedLocker = this.lockers.find(locker => locker.id === parseInt(this.selectedLockerId));
          this.form.assignedLocker = selectedLocker;
        }
        console.log('Selected locker:', this.form.assignedLocker);
      }
      this.showLockerDialog = false;
      // 
      this.selectedLockerId = '';
      this.searchQuery = '';
      this.filteredLockers = [...this.lockers];
    },
    
    // 
    // 
    filterGroups() {
      if (!this.groupSearchQuery) {
        this.filteredGroups = [...this.groups];
      } else {
        const query = this.groupSearchQuery.toLowerCase();
        this.filteredGroups = this.groups.filter(group => 
          group.name.toLowerCase().includes(query)
        );
      }
    },
    // 
    isGroupSelected(groupId) {
      return this.selectedGroupIds.includes(groupId);
    },
    // 
    toggleGroupSelection(groupId) {
      if (this.isGroupSelected(groupId)) {
        // 
        this.selectedGroupIds = this.selectedGroupIds.filter(id => id !== groupId);
      } else {
        // 
        this.selectedGroupIds.push(groupId);
      }
    },
    // 
    confirmGroupSelection() {
      // ID
      this.selectedGroups = this.groups.filter(group => 
        this.selectedGroupIds.includes(group.id)
      );
      console.log('Selected groups:', this.selectedGroups);
      this.showGroupDialog = false;
      // 
      this.selectedGroupIds = [];
      this.groupSearchQuery = '';
      this.filteredGroups = [...this.groups];
    },
    // 
    removeGroup(groupId) {
      this.selectedGroups = this.selectedGroups.filter(group => group.id !== groupId);
    }
  }
};
</script>

<style scoped>
@media (min-width: 1200px) {
    .container {
        width: 1280px!important;
    }
}
.radio label, .checkbox label

 {
    width: 30%;}
.add-new-user-page {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.page-header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 15px;
  border: 0;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.back-icon {
  font-size: 18px;
  color: #666;
  cursor: pointer;
}

.back-icon:hover {
  color: #1890ff;
}

.page-title {
  margin: 0;
  font-size: 22px;
  font-weight: 600;
  color: #333;
}

.header-right {
  width: 120px;
}

.user-form {
  width: 100%;
}

.form-row {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px !important;
}

.device-management-group {
  padding-bottom: 15px;
  margin-bottom: 25px !important;
  border-bottom: 1px solid #eee;
}

.device-management-label {
  font-weight: 600;
  margin-bottom: 10px;
  color: #333;
  display: block;
}

.locker-button-container, .group-button-container {
  margin-top: 5px;
  min-width: 200px;
}

.radio {
  margin-bottom: 20px;
}

.last-group {
  margin-top: 10px !important;
}

.inline-group {
  display: flex;
  align-items: center;
}

/*  */
.radio-group-container {
  display: flex;
  align-items: center;
  gap: 15px; /*  */
  flex-wrap: wrap;
}

.device-count-selector {
  width: 120px; /*  */
}

.save-btn-container {
  margin-top: 30px;
  padding-top: 15px;
  border-top: 1px solid #eee;
}

.dialog-text {
  color: #333;
  line-height: 1.8;
  margin: 8px 0;
  text-align: center;
  font-size: 14px;
}

.btn-primary {
  background-color: #0C306C !important;
  border-color: #0C306C !important;
  color: #FFF !important;
}

.modal-footer {
    border: none!important;
  display: flex;
  gap: 16px;
}

/* Locker */
.modal.in .modal-dialog{
  margin-top: 15%;
}
.modal-title{
    font-weight: 800;
}
.locker-modal, .group-modal {
  width: 650px !important;
}

.locker-header, .group-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border: none;

}

.search-bar {
  display: flex;
  align-items: center;
  gap: 8px;
}

.locker-search, .group-search {
  padding: 6px 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 180px;
}

.locker-body, .group-body {
  padding:0 20px;
}

.selection-prompt {
  margin-bottom: 15px;
  color: #666;
  font-size: 14px;
}

.lockers-grid, .groups-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
  margin-bottom: 20px;
}

.locker-item, .group-item {
  padding: 10px 5px;
  border: 1px solid #757575;
  color: #000;
  border-radius: 4px;
  background-color: white;
  cursor: pointer;
  transition: all 0.2s;
  text-align: center;
}

.locker-item:hover, .group-item:hover {
  border-color: #0C306C;
}

.locker-item.selected, .group-item.selected {
  background-color: #0C306C;
  color: white;
  border-color: #0C306C;
}

.unlimited {
  grid-column: span 1;
}

.locker-footer, .group-footer {
  display: flex;
  justify-content: center;
  gap: 10px;
  padding: 20px;
  border-top: 1px solid #e5e5e5;
}

.cancel-btn {
  background-color: white;
  color: #333;
  border: 1px solid #ddd;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
}

.confirm-btn {
  background-color: #0C306C;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
}

/* Groups */
.groups-display-area {
  margin-top: 15px;
  padding: 15px;
  background-color: #f5f5f5;
  border-radius: 4px;
  min-height: 60px;
}

.selected-groups {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.group-tag {
  background-color: #e0e0e0;
  padding: 5px 10px;
  border-radius: 15px;
  display: inline-flex;
  align-items: center;
  gap: 5px;
}

.remove-group {
  cursor: pointer;
  font-weight: bold;
  color: #666;
}

.remove-group:hover {
  color: #000;
}

.empty-state {
  color: #999;
  font-style: italic;
}
</style>


