<template>
  <div>
    <div class="row">
      <!--  -->
      <div class="col-xs-2 col-xs-offset-" v-if="isMenuVisible">
        <div class="list-group custom-nav">
          <!--  -->
          <div class="list-group-item dynamic-number-btn">
            <img src="./menu_img/menu.png" alt="Number">
            <span style="background-color: rgb(245 245 245);color: #000;font-size: 20px; font-weight: 600;" v-if="showMenuNumber">
              {{ menuNumber }}
            </span>
          </div> 
          
          <!--  -  -->
          <router-link 
            to="/home" 
            class="list-group-item"
            @click.native="handleHomeLockersClick"
          >
            <img src="./menu_img/home.png" alt="Home">
            <span>Home</span>
          </router-link>
          <router-link 
            to="/lockers" 
            class="list-group-item"
            @click.native="handleHomeLockersClick"
          >
            <img src="./menu_img/phone_android.png" alt="Home">
            <span>Lockers</span>
          </router-link>
          
          <!-- 6 -->
          <router-link to="/Notifications" class="list-group-item" v-if="showExtraMenus">
            <img src="./menu_img/ad.png" alt="Home">
            <span>Notifications</span>
          </router-link>
          <router-link to="/devices" class="list-group-item" v-if="showExtraMenus">
            <img src="./menu_img/reports.png" alt="Home">
            <span>Devices</span>
          </router-link>
          <router-link to="/devices_types" class="list-group-item" v-if="showExtraMenus">
            <img src="./menu_img/types.png" alt="Home">
            <span>Devices Types</span>
          </router-link>
          <router-link to="/Device_Groups" class="list-group-item" v-if="showExtraMenus">
            <img src="./menu_img/deviceGroups.png" alt="Home">
            <span>Groups</span>
          </router-link>
          <router-link to="/Users_1" class="list-group-item" v-if="showExtraMenus">
            <img src="./menu_img/users.png" alt="Home">
            <span>Users</span>
          </router-link>
          <router-link to="/BayManagement_1" class="list-group-item" v-if="showExtraMenus">
            <img src="./menu_img/bayManagement.png" alt="Home">
            <span>BayManagement</span>
          </router-link>
          
          <!-- Logout -->
          <router-link 
            to="/login_index" 
            class="list-group-item hide-menu-btn"
            @click.native="handleLogout"
          >
            <img src="./menu_img/heid.png" alt="Hide Menu">
            <span style="color: #FFF;font-weight: 600;">Logout</span>
          </router-link>
        </div>
      </div>
      <!--  -->
      <div :class="isMenuVisible ? 'col-xs-10' : 'col-xs-12'">
        <div class="panel">
          <div class="panel-body">
            <keep-alive>
              <router-view msg="abc"></router-view>
            </keep-alive>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isMenuVisible: true,
      showExtraMenus: false,
      menuNumber: '',
      showMenuNumber: false  // 
    }
  },
  created() {
    // 
    this.$root.$on('showExtraMenus', () => {
      this.showExtraMenus = true;
    });
    // 
    this.$root.$on('updateMenuNumber', (number) => {
      this.menuNumber = number;
      this.showMenuNumber = true;  // 
    });
  },
  watch: {
    $route(to) {
      this.isMenuVisible = to.path !== '/login_index';
    }
  },
  methods: {
    // 
    handleLogout() {
      this.showExtraMenus = false;
    },
    // HomeLockers
    handleHomeLockersClick() {
      this.showExtraMenus = false;
      this.menuNumber = '';
      this.showMenuNumber = false;
    }
  },
  mounted() {
    this.isMenuVisible = this.$route.path !== '/login_index';
  },
  beforeDestroy() {
    // 
    this.$root.$off('showExtraMenus');
    this.$root.$off('updateMenuNumber');
  }
}
</script>

<style>
/*  */
.dynamic-number-btn {
  margin-bottom: 0px!important;
  flex-direction: row !important;
  align-items: center !important;
  justify-content: center;
  background-color: #f5f5f5 !important; /*  */
  color: #000 !important; /*  */
}

.dynamic-number-btn img {
  margin-bottom: 0 !important;
  margin-right: 8px !important;
}

/*  */
.custom-nav {
  box-shadow: 4px 0px 16px 0px #0000001F;
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 60px);
  position: sticky; /*  */
  top: 0; /*  */
}

.custom-nav .list-group-item {
  background-color: #fff;
  border: none;
  padding: 15px 10px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.3s ease;
  text-decoration: none;
  color: #333;
  /*  */
  height: 80px;
  box-sizing: border-box;
}

.custom-nav .list-group-item img {
  margin-bottom: 8px;
  /*  */
}

.custom-nav .list-group-item:hover {
   height: 80px;
  background-color: #ff9900;
  color: #fff;
}

/*  */
.custom-nav .list-group-item.router-link-exact-active {
   height: 80px;
  background-color: #ff9900;
  color: #fff;
}

.hide-menu-btn {
  margin-top: auto;
  flex-direction: row !important;
  align-items: center !important;
  justify-content: center;
  background-color: #0C306C !important;
  color: #fff !important;
}

.hide-menu-btn img {
  margin-bottom: 0 !important;
  margin-right: 8px !important;
}
</style>


